body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.search {
  display: flex;
  width: 100%;
  justify-content: center;
  padding: 3rem 0;
}

.search input {
  width: 30%;
  background-color: #F9F0DA;
  font-size: 18px;
  padding: 12px 24px;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'%3E%3Cpath d='M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z'/%3E%3Cpath d='M0 0h24v24H0z' fill='none'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-size: 18px 18px;
  background-position: 95% center;
  border-radius: 50px;
  border: 2px solid #3E5151;
}

.search input:focus {
  outline: 0;
}

.artist-card{
  width: 230px;
  background-color: rgba(0,0,0,0.80);
  color: #fff;
  margin-bottom: 2rem;
  align-content: center;
  border: 2px solid #fff;
}

.artist-card:hover {
  cursor: pointer;
}

.artist-card img {
  width: 100%;
}

.name  {
  margin-top: -1.3rem;
  height: 50px;
}

.artist-card .name p {
  padding-top: 10px;
  padding-left: 10px;
  font-size: 18px;
}

.artist-card:hover .name {
  background-color: rgba(153,23,60,0.80);
}

.album-card {
  align-self: center;
  margin-top: 2rem;
  position: relative;
  color: #fff;
}

.album-info {
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba(0,0,0,0.75);
  width: 100%;
  line-height: 0.3rem;
}

.info p {
  font-size: 1.1rem;
  line-height: 1.3rem;
}

.album-hover {
  background-color: rgba(0,0,0,0.90);
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.album-hover p {
  font-size: 0.8rem;
  margin: 0.5rem;

}

.album-hover:hover {
  opacity: 1;
}

.inside {
  cursor: pointer;
  display: inline-block;
  padding-left: 1rem;

}

.inside p:hover {
  color: #d05959;
}

.wrapper {
  width: 100%;
  background: #4ECDC4;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #556270, #4ECDC4);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#556270), to(#4ECDC4));
  background: -webkit-linear-gradient(left, #556270, #4ECDC4);
  background: linear-gradient(to right, #556270, #4ECDC4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
}

.back {
  display: flex;
  justify-content: flex-end;
}

.back img:hover {
  cursor: pointer;
}

.artists-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.album-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.noresults {
  padding-top: 4rem;
  font-size: 22px;
}

html {
  min-height: 100%;
  background: #4ECDC4;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #556270, #4ECDC4);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#556270), to(#4ECDC4));
  background: -webkit-linear-gradient(left, #556270, #4ECDC4);
  background: linear-gradient(to right, #556270, #4ECDC4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

 body {
  font-family: "Lucida Grande", Arial, Helvetica, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  background-repeat:no-repeat;
  background-attachment: fixed;
  height: auto;
}

